@import "functions";
@import "placeholders";

// medium query
$breakpoints: ('mob-sm': 320px, 'mob-md': 480px, 'mob-lg': 640px, 'tab-sm': 768px, 'tab-lg': 960px, 'desk-sm': 1200px, 'desk-md': 1280px, 'desk-lg': 1600px, 'desk-hd': 1920px);

// medium query
$mq-desk-hd: 1919px;
$mq-desk-lg: 1599px;
$mq-desk-md: 1279px;
$mq-tab-lg: 991px;
$mq-tab-sm: 767px;
$mq-mob-lg: 639px;
$mq-mob-md: 479px;

// base
$font-size: 1.6rem !default;
$line-height: round-float(3.0rem/$font-size, 2) !default;

$font-size-small: 1.4rem !default;
$line-height-small: 1.43 !default;
// ===============
$font-size-h1: 3.6rem !default;
$line-height-h1: 1.2 !default;
$font-size-h1--sm: 2.4rem !default;
$line-height-h1--sm: 1.3 !default;
// ===============
$font-size-h2: round-float($font-size-h1*0.78, 1) !default;
$line-height-h2: round-float(($line-height-h1*1.08),1) !default;
$font-size-h2--sm: round-float($font-size-h1--sm*0.8, 1) !default;
$line-height-h2--sm: round-float(($line-height-h1--sm*1.08),1) !default;
// ===============
$font-size-h3: round-float($font-size-h1*0.56, 1) !default;
$line-height-h3: round-float(($line-height-h1*1.08),1) !default;
$font-size-h3--sm: round-float($font-size-h1--sm*0.7, 1) !default;
$line-height-h3--sm: round-float(($font-size-h1--sm*1.10),1) !default;
// ===============
$font-size-h4: round-float($font-size-h1*0.44, 1) !default;
$line-height-h4: round-float(($line-height-h1*1.33),1) !default;
$font-size-h4--sm: round-float($font-size-h1--sm*0.6, 1) !default;
$line-height-h4--sm: round-float(($font-size-h1--sm*1.12),1) !default;

// нижние отступы у элементов
$bottom-space: round-float($font-size * $line-height, 1) !default;
$bottom-space-sm: round-float($bottom-space * 0.5, 1) !default;
$bottom-space-md: round-float($bottom-space * 1.5, 1) !default;
$bottom-space-lg: round-float($bottom-space * 2, 1) !default;

// color
$base-color: #006ab8 !default;
$bg-color: #e5e5e5 !default;
$bg-color-alt: #fff !default;
$bg-color-light: #f3f3f3 !default;
$text-color: #262626 !default;
$text-color-light: #999 !default;
$text-color-ultralight: #f3f3f3 !default;
$text-color-alt: $base-color !default;
$mark-color: #ec3c01 !default;
$mark-color-alt: #ff9a09 !default;
$title-color: #010101 !default;
$link-color: $base-color !default;
$border-color: #d9d9d9 !default;

// user content class
// class на контейнере, в котором будет использоваться стилизация
// типографических элементов: списков, таблиц, цытат и т.д.
$user-content: user-content !default;

// form color
$color-form-text: #333 !default;
$color-form-placeholder: #ccc !default;
$color-form-bg: #fff !default;
$color-form-border: #d9d9d9 !default;
$color-form-border-focus: $base-color !default;
$color-form-error: #ec3c01 !default;
$color-form-success: #056e41 !default;

// btn color
$btn-bg-color: $text-color-alt !default;
$btn-bg-color-hover: shade($btn-bg-color, 80%);
$btn-text-color: #fff !default;
$btn-disabled-color: #ccc !default;

// table color
$color-table-text: #262626 !default;
$color-table-text-alt: #fff !default;
$color-table-bg: #fff !default;
$color-table-bg-alt: $base-color !default;
$color-table-bg-row: #fafafa !default;

// social color
$facebook-color: #3a589e;
$vkontakte-color: #4b73a4;
$twitter-color: #1da0f2;
$odnoklassniki-color: #f2822e;
$instagram-color-start: #de4f5a;
$instagram-color-end: #a43b97;

// grids
$grid-space: 20px !default; // отступы между элементами сетки, например, новостей
$grid-inside-horizontal-space: round($grid-space*1.5) !default; // горизонтальные отступы внутри элементов сетки
$grid-inside-vertical-space: round($grid-inside-horizontal-space*0.85) !default; // вертикальные отступы внутри элементов сетки
$news-label-size: 50px !default; // размер меток содержания статьи: видео, аудио, pdf и т.д.

// new
// размеры подобранны и заданы хардкодом, чтобы сохранить полную идентичность дизайну без применения js
//$news-item-height: 299px !default // высота стандартного элемета превью новостей
$news-item-height: 327.9px !default; // высота стандартного элемета превью новостей
$news-item-width-base: 270px !default; // ширина стандартного элемета превью новостей без учета бокового отступа
$news-item-width: $news-item-width-base+$grid-space !default; // ширина стандартного элемета превью новостей с учетом боковых отступов
$news-item-img-height: 154px !default; // высота картикнки
$news-item-content-height: $news-item-height - $news-item-img-height !default; // высота контента превью новостей
//$news-item-sm-height: 154px !default // высота элемента превью новостей без изображения
$news-item-sm-height: round($news-item-height/2)-(round($grid-space/2)) !default; // высота элемента превью новостей без изображения
$news-item-sm-width: $news-item-width !default; // ширина элемента превью новостей без изображения (такая же как и у стандартного)
$news-item-sm-content-height: $news-item-sm-height !default; // высота контента превью новостей без изображения
$news-item-lg-height: ($news-item-height*2+$grid-space)-0.1 !default; // высота большого элемета
$news-item-lg-width: $news-item-width*2 !default; // ширина большого элемета
$news-item-lg-img-height: 350px !default; // высота картикнки в большом блоке
$news-item-lg-img-height-mob-lg: 200px !default; // для разрешений до 640
$news-item-lg-content-height: $news-item-lg-height - $news-item-lg-img-height !default; // высота контента больших превью новостей
$news-item-lg-content-height-mob-lg: $news-item-lg-height - $news-item-lg-img-height-mob-lg !default; // высота контента больших превью новостей (для разрешений до 640)

// sidebars
$sidebar-left-width: 270px !default;
$sidebar-right-width: 270px !default;
$sidebar-right-width--desk-lg: 225px !default;
$sidebar-right-width--desk-md: 160px !default;
$sidebar-inner-space-horizontal: 30px !default;

// layout
$main-layout-width: 1903px !default;
$main-layout-space-horizontal: 0 !default;
$grid-layout-space: 60px !default;
$grid-layout-space--desk-hd: 50px !default;
$grid-layout-space--desk-lg: 40px !default;
$grid-layout-space--desk-md: 30px !default;
$grid-layout-space--mob-lg: 15px !default;
$article-layout-width: none !default;
$article-layout-space-horizontal: 50px !default;
$article-layout-space-vertical: round($article-layout-space-horizontal*0.9) !default;
$article-layout-space-horizontal--desk-hd: 30px !default;
$article-layout-space-vertical--desk-hd: round($article-layout-space-horizontal--desk-hd*0.85) !default;
$article-layout-space-horizontal--tab-lg: 25px !default;
$article-layout-space-vertical--tab-lg: round($article-layout-space-horizontal--tab-lg*0.8) !default;
$article-layout-space-horizontal--mob-lg: 15px !default;
$article-layout-space-vertical--mob-lg: round($article-layout-space-horizontal--mob-lg*0.8) !default;
$text-layout-width: 640px !default;
$text-layout-space-horizontal: 0 !default;
$section-layout-width: null !default;

$section-layout-width-desk-hd: $news-item-width-base*3+$grid-space*2+$sidebar-right-width+$grid-layout-space--desk-hd !default;
$section-layout-width-desk-lg: $news-item-width-base*2+$grid-space*1+$sidebar-right-width--desk-lg+$grid-layout-space--desk-lg !default;
$section-layout-width-tab-lg: $news-item-width-base*2+$grid-space !default;
$section-layout-width-mob-sm: $news-item-width-base !default;

// events-summary (только для текущего проекта)
$events-summary-nav-height: 60px !default;

// obzh (только для текущего проекта)
$obzh-tabs-nav-height: 60px !default;

// form
$space-bottom-form: 3rem !default;

// side
$side-width: 60px;
$side-font-size: 14px;
$side-line-height: round($side-font-size*1.3);
$side-transition-timing-function: cubic-bezier(0.61, 0.01, 0.41, 1);

// header
$header-bg: #f7f7f7 !default;
$header-top-height: 60px !default;
$header-top-height--md: 50px !default;
$header-top-height--sm: 45px !default;
//$header-content-height: 120px !default
$header-content-height: 150px !default;
$header-content-height--md: 100px !default;
$header-height: $header-top-height + $header-content-height !default;
$header-height--md: $header-top-height--md + $header-content-height--md !default;
$header-content-height--tab-lg: 110px !default;
$header-left-space: $sidebar-left-width !default;
$header-left-space-desk-sm: 140px !default; // по дизайну
$go-to-mchs-width: 165px;

// nav
$color-nav-bg: $base-color !default;
$color-nav-bg-hover: #ff9a09 !default;
$color-nav-active: #ff9a09 !default;
$nav-item-space: 17px 17px !default;
$nav-item-space--md: 12px 14px !default;

// subsite menu
$subsite-menu-text-color: rgba(255, 255, 255, 0.50) !default;
$subsite-menu-bg-color: #5e7180 !default;
$subsite-menu-bg-color-s: shade($subsite-menu-bg-color, 80%) !default;
$subsite-menu-bg-color-ss: shade($subsite-menu-bg-color, 62%) !default;
$subsite-menu-bg-color-sss: shade($subsite-menu-bg-color, 48%) !default;
$subsite-menu-bg-color-ssss: shade($subsite-menu-bg-color, 38%) !default;
$subsite-menu-border-color: rgba(0, 0, 0, 0.20) !default;
$subsite-menu-tab-height: 70px !default;

// search form
$search-form-popup-btn-width: 130px;
$search-form-popup-btn-width--mob-lg: 54px;
$search-form-popup-btn-height: 40px;
$search-form-popup-btn-height--mob-lg: 40px;

// faq
$faq-meta-width: 180px;
$faq-angle-size: 40px;

//pagination
$pagination-space: 13px;

// animation
$animation-speed: 0.2s !default;
$animation-speed-mouseleave: 0.35s !default;

// for special version
$border-width: 2px;

// fonts style
$font-norm: 'Roboto', sans-serif !default;
$font-b: 'Roboto', sans-serif !default;
$font-i: 'Roboto', sans-serif !default;
$font-bi: 'Roboto', sans-serif !default;
$font-alt: 'Roboto Slab', serif !default;

@mixin font-reset() {
	font: {
		weight: normal;
		style: normal; } }

@mixin font-normal() {
	font: {
		family: $font-norm;
		weight: 300; }
 }	//+font-reset

@mixin font-bold() {
	font: {
		family: $font-b;
		weight: 700; }
 }	//+font-reset

@mixin font-italic() {
	font: {
		family: $font-i;
		weight: 300;
		style: italic; }
 }	//+font-reset

@mixin font-bold-italic() {
	font: {
		family: $font-bi;
		weight: 700;
		style: italic; }
 }	//+font-reset

@mixin font-title() {
	font: {
		family: 'Roboto Slab', sans-serif;
		weight: 700;
		style: normal; }
 }	//+font-reset

@mixin font-alt-norm() {
	font: {
		family: 'Roboto Slab', sans-serif;
		weight: 400;
		style: normal; }
 }	//+font-reset
